html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  --link-color: #00acdc;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo__image {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-style: normal;
  line-height: normal;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link[data-qa="hover"]:hover, .is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background: var(--link-color);
  border-radius: 8px;
  flex-shrink: 0;
  width: 37px;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

